import React, {useState, useEffect} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.css';

let Sbox = styled.div`padding : 20px;`;
let Name1 = styled.h4`color : ${ props => props.color};`;

function Detail(props){

	let [sboxState, setSboxState] = useState(true);
	let history = useHistory();
	let { id } = useParams();
	let selectItem = props.shoes.find(function(item){
		return item.id == id
	});
	let price = selectItem.price.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
	let [cnt,setCnt]	= useState('');


	useEffect(()=>{
		let timer = setTimeout(()=>{
			if(sboxState){
				setSboxState(false);
			}else{
				setSboxState(true);
			}
		},3000);

		return ()=>{clearTimeout(timer);}
	},[sboxState]);

	return (
	  <div className="container">
				{ sboxState? 
					<Sbox><Name1 color='red'>재고가 얼마 안남았습니다.</Name1></Sbox> 
					: <Sbox><Name1 color='red'>　</Name1></Sbox>
				}
			<div className="row">
				<div className="col-md-6">
				<img src={"https://codingapple1.github.io/shop/shoes"+Number(selectItem.id+1)+".jpg"} width="100%" />
				</div>
				<div className="col-md-6 mt-4">
				<h4 className="pt-5">{selectItem.title}</h4>
				<p>{selectItem.content}</p>
				<p>{price}원</p>
				수량 : <input type='text' onChange={(e)=>{setCnt(e.target.value)}}></input><br/><br/>
				<button className="btn btn-danger" onClick={()=>{
					history.push('/cart');
				}}>주문하기</button> 
				<button className="btn btn-danger" onClick={()=>{
					history.goBack();
				}}>뒤로가기</button> 
				</div>
			</div>
	  </div>
		)
  }

  export default Detail;
