/* esLint-disable */

import React, {useState, useEffect} from 'react';
import './App.css';
import { Nav, Navbar, Container, NavDropdown, Card } from 'react-bootstrap';
import dataList from './data.js';
import Detail from './Detail.js'
import axios from 'axios';
import { Link, Route, Switch, useHistory } from 'react-router-dom';

function App() {

  let [shoes, setShoes] = useState(dataList);
  let [dateMore, setDateMore]  = useState([]);

  console.log(dateMore);
  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand href="#home">simple-Shop</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link as={Link} to="/">Home</Nav.Link>
              <Nav.Link as={Link} to="/detail/0">Detail </Nav.Link>
              <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <Switch>
        <Route exact path="/">
          <Card>
            <Card.Body className="background">
              <h1>20% Season Off</h1>
              hello welcome Shop
            </Card.Body>
          </Card>

          <div className="container">
            <div className="row">
              {shoes.map((list, index) => (
                  <Cardbox shoes={list} />
              ))}
            </div>
          </div>
          <div className="btn btn-primary" onClick={()=>{

              axios.get('https://codingapple1.github.io/shop/data2.json')
              .then(res => setShoes([...shoes, ...res.data]))
              .catch(err => console.log(err));
              
          }}
          >더보기</div>
        </Route>
        <Route path="/detail/:id">
              <Detail shoes={shoes}/>
        </Route>

      </Switch>

    </div>
  );
}

function Cardbox(props){

  let history = useHistory();
  let price = props.shoes.price.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");

  return (
  <div className="col-md-4">
  <img width="300" src={"https://codingapple1.github.io/shop/shoes"+Number(props.shoes.id+1)+".jpg"} onClick={()=>{
    history.push('/detail/'+props.shoes.id);
  }}/>
  <h4>{props.shoes.title}</h4>
  <h4>{price}원</h4>
  </div>
  )
}

export default App;